import Vue from 'vue';
import VueRouter from 'vue-router';

Vue.use(VueRouter);

const routes = [
  {
    path: '/admin',
    component: require('./components/about.vue').default
  },
  {
    path: '/admin/blog',
    component: require('./components/blog.vue').default
  },
  {
    path: '/admin/works',
    component: require('./components/works.vue').default
  }
];

export default new VueRouter({
  routes,
  mode: 'history'
});



