const skills = {
  state: {
    data: []
  },
  getters: {
    skills(state) {
      return state.data;
    }
  },
  mutations: {
    addNewSkill(state, skill) {
      state.data.push(skill);
    },
    removeSkill(state, skillId) {
      state.data = state.data.filter(skill => skill.id !== skillId);
    }
  },
  actions: {
    loadSkills({state}, url) {
      return fetch(url)
        .then(response => response.json())
        .then(json => (state.data = json.skills))
        .catch(error => alert(error));
    },
    uploadSkills({state}, url) {
      const myHeaders = new Headers();
      myHeaders.append('Content-Type', 'application/json');

      const settings = {
        body: JSON.stringify(state.data),
        headers: myHeaders,
        method: 'PUT'
      };
      return fetch(url, settings).catch(error => console.error(error));
    }
  }
};

export default skills;



