<template lang="pug">
  div.list
    h3.title {{skillType}}
    table.table
      skill-item(
        v-for="(skill, index) in skills"
        :key="skill.id",
        :skill="skill",
        :validate="validate",
        @validationSuccess="validationSuccess",
        @validationFail="validationFail",
        v-if="skillType === skill.type")
</template>

<script>
  import skillItem from './skillItem';

  export default {
    props: {
      skillType: String,
      skills: Array,
      validate: Boolean
    },
    methods: {
      validationSuccess() {
        this.$emit('validationSuccess');
      },
      validationFail() {
        this.$emit('validationFail');
      }
    },
    components: {
      skillItem
    }
  };
</script>

<style lang="scss" src="styles/skillsList.scss" scoped>

</style>



