<template lang="pug">
  section.about
    .page-wrapper
      h2.title   
      form.form(:action="url", method="PUT", @submit.prevent="uploadData")
        .form-content
          skills-list(
            v-for="(skillType, index) in skillsTypes",
            :skillType="skillType",
            :key="index",
            :skills="skills",
            :validate="validate",
            @validationFail="validationFail",
            @validationSuccess="validationSuccess"
          )
        button.button.button--form(type="submit") 
    .wrapper(:class="{'wrapper--active': submitted}")
      .popup
        p.message {{response}}
        button.button.button--popup(@click="closePopup") 

</template>

<script>
  import skillsList from './skillsList';
  import {mapActions, mapGetters} from 'vuex';

  export default {
    data() {
      return {
        url: '/api/skills',
        submitted: false,
        validate: false,
        validInputs: 0,
        response: ''
      }
    },
    computed: {
      ...mapGetters(['skills']),
      skillsTypes: {
        get() {
          return this.skills.reduce((acc, skill) => {
            if (!acc.includes(skill.type)) {
              acc.push(skill.type);
            }

            return acc;
          }, []);
        }
      }
    },
    methods: {
      ...mapActions(['loadSkills', 'uploadSkills']),
      uploadData(evt) {
        this.validate = true;
        this.validInputs = 0;
      },
      closePopup() {
        this.submitted = false;
      },
      openPopup() {
        this.submitted = true;
      },
      validationSuccess() {
        this.validInputs++;

        if (this.validInputs !== this.skills.length) return false;

        this.uploadSkills(this.url)
          .then(response => response.json())
          .then(json => this.response = json.msg)
          .then(() => {
            this.openPopup();
            this.validate = false;
          });
      },
      validationFail() {
        this.validate = false;
      }
    },
    created() {
      this.loadSkills(this.url);
    },
    components: {
      skillsList
    }
  };
</script>

<style lang="scss" src="styles/about.scss" scoped>

</style>



