<template lang="pug">
  section.blog
    .page-wrapper
      h2.title  
      form.form(:action="url", method="POST", @submit.prevent="upload")
        h3.form-title  
        label.label(for="title")
          input.input(
            :class="{'input--error': validation.hasError('name')}",
            type="text",
            id="title",
            name="title",
            placeholder="",
            v-model="name"
          )
          span.visually-hidden 
          div.error(:class="{'error--active': validation.hasError('name')}") {{validation.firstError("name")}}
        label.label(for="date")
          input.input(
            :class="{'input--error': validation.hasError('date')}",
            type="text",
            id="date",
            name="date"
            placeholder="",
            :value="date"
          )
          span.visually-hidden 
          div.error(:class="{'error--active': validation.hasError('date')}") {{validation.firstError("date")}}
        label.label(for="message")
          textarea.text(
            :class="{'input--error': validation.hasError('text')}",
            type="text",
            id="message",
            name="message",
            placeholder="",
            v-model="text"
          )
          span.visually-hidden 
          div.error(:class="{'error--active': validation.hasError('text')}") {{validation.firstError("text")}}
        button.button(type="submit") 
    .wrapper(:class="{'wrapper--active': submitted}")
      .popup
        p.message {{response}}
        button.button(@click="closePopup") 
</template>

<script>
  import {Validator} from 'simple-vue-validator';

  export default {
    mixins: [require('simple-vue-validator').mixin],
    validators: {
      'name'(value) {
        return Validator.value(value).required('   ');
      },
      'date'(value) {
        return Validator.value(value).required('   ');
      },
      'text'(value) {
        return Validator.value(value).required('   ');
      }
    },
    computed: {
      date: {
        get() {
          const now = new Date();
          const year = now.getFullYear();
          const month = now.getMonth();
          const day = now.getDate();
          return `${year}-${month < 9 ? '0' + (month + 1) : month + 1}-${day}`;
        }
      }
    },
    data() {
      return {
        url: '/api/blog',
        submitted: false,
        name: '',
        text: '',
        response: ''
      }
    },
    methods: {
      closePopup() {
        this.submitted = false;
      },
      openPopup() {
        this.submitted = true;
      },
      upload() {
        this.$validate().then(success => {
          if (!success) return false;

          this.uploadData()
            .then(response => response.json())
            .then(json => this.response = json.msg)
            .then(this.openPopup);;
        });
      },
      uploadData() {
        const settings = {
          body: JSON.stringify({
            title: this.name,
            date: this.date,
            text: this.text
          }),
          headers: {
            'Content-type': 'application/json'
          },
          method: 'POST'
        };
        return fetch(this.url, settings).catch(error => console.error(error));
      }
    }
  };
</script>

<style lang="scss" src="styles/blog.scss" scoped>

</style>



